body {
    overflow: hidden;
    width: 100vw;
    height: 100vh;
}

#app {
    
}

.level-one {
    display: flex;
    width: 100vw;
    height: 100vh;
}

.level-one-left {
    width: 50%;
    position: relative;
    background: url(/pics/tmk.jpg);
    background-size: cover;
    background-position: 90% 50%;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    transition: width 2s ease-in-out;
    overflow: hidden;
    z-index: 1;

    .level-one-panel {
        right: 0;
    }

    &--openLeft {
        width: 100%;
        z-index: 1;
        .level-one-panel {
            right: -645px;
            transition: right 1s ease-in-out;
        }
        .level-one__more {
            &::after {
                display: none;
            }
        }
    }

    &--openRight {
        //left: -50%;
        width: 0%;
        .level-one-panel {
            right: -645px;
            transition: right 1s ease-in-out;
        }
        .level-one__more {
            &::after {
                display: none;
            }
        }
    }
}

.level-one-right {
    width: 100%;
    position: relative;
    background: url(/pics/stz.jpg);
    background-size: cover;
    position: absolute;
    top: 0;
    right: 0;
    height: 100vh;
    transition: width 2s ease-in-out;
    overflow: hidden;

    .level-one-panel {
        right: 0;
    }

    &--openRight {
        //right: 0%;
        width: 100%;
        background-position: 0% 50%;
        .level-one-panel {
            right: -645px;
            transition: right 1s ease-in-out;
        }
        .level-one__more {
            &::after {
                display: none;
            }
        }
    }

    &--openLeft {
        //right: -50%;
        width: 0;
        .level-one-panel {
            right: -645px;
            transition: right 1s ease-in-out;
        }
        .level-one__more {
            &::after {
                display: none;
            }
        }
    }
}

.level-one-panel {
    padding: 0px 30px;
    width: 615px;
    background: #eeeeee;
    height: 100vh;
    position: absolute;
    top: 0;
    //right: 0;
    transition: right 1s ease-in-out 1s;
    transition: left 1s ease-in-out 1s;
    overflow: hidden;
}

.level-two {
    position: absolute;
    width: 0;
    height: 100vh;
    top: 0;
    right: 0%;
    transition: width 2s ease-in-out 0s;
    overflow: hidden;
    z-index: 2;
}


.level-three {
    position: absolute;
    width: 0;
    height: 100vh;
    top: 0;
    right: 0%;
    transition: width 2s ease-in-out;
    overflow: hidden;
    z-index: 3;
}

.level-four {
    position: absolute;
    width: 0;
    height: 100vh;
    top: 0;
    right: 0%;
    transition: width 2s ease-in-out;
    overflow: hidden;
    z-index: 4;
}


.level-three-left {
    width: 100%;
    height: 100vh;
}

.level-three-right {
    width: 900px;
    position: absolute;
    top: 0;
    right: 1020px;
}


.level-two--open {
    width: 100%;
    transition: width 2s ease-in-out 1.5s;
}

.level-three--open {
    width: 100%;
}

.level-four--open {
    width: 100%;
}

.level-two--hidden {
    width: 100%;
}

.level-three--hidden {
    width: 100%;
}


.level-four-left {
    width: 100%;
    height: 100vh;
}

.level-four-right {
    width: 960px;
    position: absolute;
    top: 0;
    right: 960px;
    z-index: 1;
}

.level-two-left {
    width: 100%;
    height: 100vh;
}

.level-two-right {
    width: 700px;
    position: absolute;
    top: 0;
    right: 100px;
    z-index: 1;
}

.level-two-panel {
    padding: 100px 80px;
    background: #eeeeee;
    height: 100vh;
}


.level-one__logo {
	
}

.level-one__logo--tmk {
    margin: 100px 0 80px;
}

.level-one__logo--sinz {
    margin: 300px 0 40px;
}

.level-one__title {
    font-size: 60px;
    line-height: 72px;
    margin-bottom: 90px;
}

.level-one__more {
    font-family: 'Europe';
    background: #f36e21;
    padding: 34px 100px 32px 40px;
    width: 315px;
    height: 90px;
    color: #fff;
    font-size: 24px;
    border-radius: 50px;
    position: relative;
    cursor: pointer;
    box-shadow: 0 0px 0px rgba(0, 0, 0, 0.12);
    overflow: hidden;

    span {
        position: absolute;
        top: 35px;
        left: 40px;
        display: block;
        animation: move 5s infinite ease-in-out 1s;
    }

    /*&--alt {
        span {
            animation: move 6s infinite ease-in-out 3s;
        }
        
        .level-one__alt {
            animation: move2 6s infinite ease-in-out 3s;
        }
    }*/
}



.level-one__alt {
    display: block;
    position: absolute;
    top: -50px;
    left: 40px;
    animation: move2 5s infinite ease-in-out 1s;
}

@keyframes move {
    from {
        top: 35px;
        opacity: 1;      
    }

    30% {
        top: 35px;
        opacity: 1;    
    }

    35% {
        top: 100px;
        opacity: 1;    
    }

    37% {
        top: 100px;
        opacity: 0;   
    }

    40% {
        top: -50px;
        opacity: 0;  
    }

    55% {
        top: -50px;
        opacity: 1;  
    }

    65% {
        top: 35px;
        opacity: 1;  
    }

    to {
        top: 35px;
        display: block;
    }
}

@keyframes move2 {
    from {
        top: -50px;
        opacity: 1;      
    }

    30% {
        top: -50px;
        opacity: 1;   
    }

    45% {
        top: 35px;
        opacity: 1;   
    }

    50% {
        top: 35px;
        opacity: 1;   
    }

    55% {
        top: 100px;
        opacity: 1;  
    }

    60% {
        top: 100px;
        opacity: 0;  
    }

    65% {
        top: -50px;
        opacity: 0;  
    }

    to {
        top: -50px;
        opacity: 1;
    }
}



.level-one__more-arrow {
    position: absolute;
    top: 10px;
    right: 10px;
    border-radius: 50%;
    height: 70px;
    width: 70px;
    background: #f58b4d;
    overflow: hidden;

    &:before {
        content: "";
        background: url(/pics/arrow.png);
        width: 30px;
        height: 20px;
        position: absolute;
        top: 25px;
        left: 21px;
    }
}

.level-one__qr {
    margin-top: 40px;
}




.content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;

	h1 {
        font-size: 50px;
        margin-bottom: 40px;
    }

    p {
        font-size: 18px;
        font-family: 'Europe';
    }
}

.content-block {
	margin-bottom: 40px;
}

.content-back {
	font-family: 'Europe';
    border: 2px solid #f36e21;
    padding: 34px 60px 32px 100px;
    width: 315px;
    color: #f36e21;
    font-size: 24px;
    border-radius: 50px;
    position: relative;
    cursor: pointer;
    text-align: center;

}

.content-back--arrow {
	position: absolute;
    top: 10px;
    left: 10px;
    border-radius: 50%;
    height: 70px;
    width: 70px;
    background: #f36e21;
    &:before {
        content: "";
        background: url(/pics/arrow.png);
        transform: rotate(180deg);
        width: 30px;
        height: 20px;
        position: absolute;
        top: 25px;
        left: 21px;
    }
}

.breadcrumbs {
    font-size: 18px;
    font-family: 'Europe';
    color: #939393;
    margin-bottom: 30px;
}

.breadcrumbs-one {
    display: inline-block;
    text-decoration: underline;
    margin-bottom: 10px;
    cursor: pointer;
}

.breadcrumbs-del {
    display: inline-block;
	margin: 0 5px;
}

.active {
	text-decoration: none;
}